<template>
  <div class="rd-table">
    <!--  -->
    <div class="rd-table-header">
      <div class="rd-table-header-left">
        <div
          v-for="(item, index) in headers_"
          :key="index"
          class="rd-table-header-item"
        >
          <!--  -->
          <template v-if="item.type === 'slot'">
            <slot
              :name="item.slotName"
              v-bind="item"
              :selections="selections"
            ></slot>
          </template>
          <!--  -->
          <el-button v-else v-bind="item.props" @click="btnClick(item)">
            {{ item.label }}
          </el-button>
        </div>
      </div>
      <div class="rd-table-header-right">
        <!--  -->
        <div v-if="visibleColumns_.length > 0" class="visibleCols">
          <el-dropdown trigger="click" :hide-on-click="false">
            <i class="el-icon-setting"></i>
            <template v-slot:dropdown>
              <el-dropdown-menu class="rd-dropdown-menu visibleCols-list">
                <el-dropdown-item
                  v-for="(item, index) in visibleColumns_"
                  :key="index"
                >
                  <div class="visibleCols-item">
                    <el-checkbox
                      v-model="item.visible"
                      class="checkbox"
                      @change="visibleChange"
                    ></el-checkbox>
                    <span class="ellipsis">{{ item.label }}</span>
                  </div>
                </el-dropdown-item>
              </el-dropdown-menu>
            </template>
          </el-dropdown>
        </div>
      </div>
    </div>
    <div class="rd-table-body">
      <el-table
        ref="el-table"
        v-bind="table_"
        :data="tableData_"
        @selection-change="selectionChange"
        @sort-change="sortChange"
        @cell-click="cellClick"
        @cell-dblclick="cellDblclick"
        @row-click="rowClick"
        @row-contextmenu="rowContextmenu"
        @row-dblclick="rowDblclick"
        @expand-change="expandChange"
      >
        <!--  -->
        <el-table-column
          v-if="multipleTable"
          type="selection"
          width="35"
          :class-name="multipleTable === 'radio' ? 'multipleTable-radio' : ''"
          :fixed="hasFixed"
          :selectable="table_.selectable"
        />
        <template v-for="(column, index) in tableColumns_">
          <!--  elementtabletree -->
          <el-table-column
            v-if="!column.type"
            :key="index"
            v-bind="column"
            :type="undefined"
          >
            <!-- headerSlotNameheaderheader -->
            <template slot="header" slot-scope="scope">
              <column-header :column="column" :index="index" :scope="scope">
                <slot
                  v-if="column.headerSlotName"
                  :name="column.headerSlotName"
                  v-bind="scope"
                />
              </column-header>
            </template>
            <template v-if="column.children && column.children.length > 0">
              <template v-for="(col, i) in column.children">
                <el-table-column
                  v-if="!col.type"
                  :key="i"
                  v-bind="col"
                  :type="undefined"
                >
                </el-table-column>
              </template>
            </template>
          </el-table-column>
          <el-table-column
            v-else
            :key="index"
            v-bind="column"
            :filtered-value="column.filteredValue"
            :type="undefined"
          >
            <!-- headerSlotNameheaderheader -->
            <template slot="header" slot-scope="scope">
              <column-header :column="column" :index="index" :scope="scope">
                <slot
                  v-if="column.headerSlotName"
                  :name="column.headerSlotName"
                  v-bind="scope"
                />
              </column-header>
            </template>
            <!-- 2 -->
            <template v-if="column.children && column.children.length > 0">
              <template v-for="(col, i) in column.children">
                <el-table-column
                  :key="i"
                  v-bind="col"
                  :filtered-value="col.filteredValue"
                  :type="undefined"
                >
                  <!-- headerSlotNameheaderheader -->
                  <template slot="header" slot-scope="scope">
                    <column-header :column="col" :index="i" :scope="scope">
                      <slot
                        v-if="col.headerSlotName"
                        :name="col.headerSlotName"
                        v-bind="scope"
                      />
                    </column-header>
                  </template>
                  <template v-if="col.type">
                    <template slot-scope="scope">
                      <!--  -->
                      <slot
                        v-if="col.type === 'slot'"
                        :name="col.slotName"
                        v-bind="scope"
                        :value="scope.row[col.prop]"
                      />
                      <!--  -->
                      <table-column
                        v-else
                        :scope="scope"
                        :column="col"
                        :dropList="dropList"
                        @change="rowDataChange"
                        @click="rowItemClick"
                      />
                    </template>
                  </template>
                </el-table-column>
              </template>
            </template>
            <!--  -->
            <template v-if="column.type" slot-scope="scope">
              <template>
                <!--  -->
                <slot
                  v-if="column.type === 'slot'"
                  :name="column.slotName"
                  v-bind="scope"
                  :value="scope.row[column.prop]"
                />
                <!--  -->
                <table-column
                  v-else
                  :scope="scope"
                  :column="tableColumns_[index]"
                  :dropList="dropList"
                  @change="rowDataChange"
                  @click="rowItemClick"
                />
              </template>
            </template>
          </el-table-column>
        </template>
      </el-table>
    </div>
  </div>
</template>
<script>
import { cloneDeep } from 'lodash';
import TableColumn from './components/tableColumn.vue';
import ColumnHeader from './components/columnHeader.vue';
export default {
  name: 'RdTable',
  components: {
    TableColumn,
    ColumnHeader
  },
  props: {
    /**
     * 
     * el-table
     */
    table: {
      type: Object,
      default: () => {
        return {};
      }
    },
    /**  */
    headers: {
      type: Object,
      default: () => {
        return {};
      }
    },
    /**
     * 
     */
    tableColumns: {
      type: Array,
      default: () => {
        return [];
      }
    },
    /**
     * 
     */
    tableData: {
      type: Array,
      default: () => {
        return [];
      }
    },
    /**
     * tableColumns
     */
    dropList: {
      type: Object,
      default: () => {
        return {};
      }
    }
  },
  data() {
    return {
      table_: {},
      /**  */
      // tableColumns_: [],
      /**  */
      visibleColumns_: [],
      /**  */
      tableColumns_All: [],
      /**  */
      tableData_All: [],
      /**  */
      tableData_: [],
      /**  */
      selections: [],
      /**  */
      hasFixed: false
      /** filterschange */
      // filtersChaneg: false,
      /**  */
      // refreshFlag: true
    };
  },
  computed: {
    /**  */
    headers_() {
      let data_clone = cloneDeep(this.headers);
      let headers = Object.entries(data_clone)
        .map(([prop, ele]) => {
          let { type, props, slotName } = ele;
          let size, btnType;
          if (type !== 'slot') {
            btnType = 'primary';
            size = 'mini';
          }

          // slotNameprop
          if (type === 'slot' && !slotName) {
            slotName = prop;
          }

          return {
            slotName,
            ...ele,
            prop,
            props: {
              type: btnType,
              size,
              ...props
            }
          };
        })
        .filter((ele) => {
          let { hidden = false } = ele;
          let flag = true;
          if (typeof hidden === 'function') {
            flag = !hidden(ele);
          } else {
            flag = !hidden;
          }
          return flag;
        });

      return headers;
    },
    /**  */
    multipleTable() {
      let { multipleTable = false } = this.table_;
      return multipleTable;
    },
    /**   */
    visibleColumns() {
      let cols = [];
      let columns = cloneDeep(this.tableColumns_All);
      let { visibleCols } = this.table_;

      // table
      let visibles = columns.filter((col) => {
        // if (Object.prototype.hasOwnProperty.calll(col, 'visible')) {
        if (Object.hasOwn(col, 'visible')) {
          return col;
        }
      });

      if (visibles.length > 0) {
        cols = visibles;
      } else {
        // table
        if (visibleCols === true) {
          cols = columns.map((col) => {
            return {
              ...col,
              visible: true
            };
          });
        } else if (Array.isArray(visibleCols)) {
          columns.forEach((col) => {
            let { prop } = col;
            if (visibleCols.indexOf(prop) !== -1) {
              cols.push({
                ...col,
                visible: true
              });
            }
          });
        }
      }
      return cols;
    },
    tableColumns_() {
      let cols = [];
      let columns = cloneDeep(this.tableColumns_All);

      // 
      columns.forEach((ele) => {
        this.handleReadonly(ele);
      });

      //  
      if (this.visibleColumns_.length === 0) {
        cols = columns;
      } else {
        cols = columns.filter((col) => {
          let visibleCol = this.visibleColumns_.find((c) => {
            return c.prop === col.prop;
          });

          if (visibleCol) {
            if (visibleCol.visible === true) {
              return col;
            }
          } else {
            return col;
          }
        });
      }

      let hasFixed = false;
      cols.forEach((col) => {
        let { fixed } = col;
        // fixed
        if (fixed) {
          hasFixed = true;
        }
      });
      this.setHasFixed(hasFixed);

      // // filters tableDatacomputed
      // cols = cols.map((col) => {
      //   return this.handleFilters(col);
      // });

      // console.log('--------------', cols);
      /** col.filtersel-table-column */
      // if (this.filtersChaneg) {
      //   this.updateRefreshFlag();
      // }
      return cols;
    }
  },
  methods: {
    /**
     * 
     */
    handleTable(data) {
      let table = cloneDeep(data);
      let { border = true, sumText = '' } = table;

      this.table_ = {
        border,
        sumText,
        ...table
      };
    },
    /**
     * 
     */
    handleTableColumns(data) {
      let columns = cloneDeep(data);
      let tableColumns = columns
        .filter((ele) => {
          return this.filterCol(ele);
        })
        .map((ele) => {
          return this.setEle(ele);
        });
      this.tableColumns_All = tableColumns;
    },

    /**  */
    filterCol(ele) {
      let { hidden = false } = ele;
      let flag = true;
      if (typeof hidden === 'function') {
        flag = !hidden(ele);
      } else {
        flag = !hidden;
      }
      if (ele.children && ele.children.length > 0) {
        ele.children.filter((e) => {
          return this.filterCol(e);
        });
      }
      return flag;
    },

    /**  */
    setEle(ele) {
      let {
        type,
        align = 'center',
        width,
        props,
        prop,
        slotName,
        ellipsis,
        formatter,
        children
      } = ele;

      if (type === 'operation') {
        width = 120;
      }

      if (!slotName) {
        slotName = prop;
      }

      // formatterstringel-tableformatterformatter
      let formatter_c;
      if (type && formatter) {
        formatter_c = formatter;
        formatter = undefined;
      }

      if (children && children.length > 0) {
        children = children.map((e) => {
          return this.setEle(e);
        });
      }

      return {
        type,
        slotName,
        align,
        width,
        ellipsis,
        props: {
          ...props
        },
        ...ele,
        formatter,
        formatter_c,
        children
      };
    },

    /**
     * 
     */
    handleTableData(data) {
      this.tableData_All = cloneDeep(data || []);
      this.tableData_ = this.tableData_All;
      this.doLayout();
    },

    /**  */
    handleReadonly(ele) {
      if (this.table_.readonly) {
        let { children } = ele;
        if (children && children.length > 0) {
          children.forEach((e) => {
            this.handleReadonly(e);
          });
        }
        ele.props = {
          ...ele.props,
          readonly: this.table_.readonly
        };
      }
    },

    /**  */
    // handleFilters(col) {
    //   let { filters, prop, children } = col;

    //   // filterstrue
    //   if (filters === true) {
    //     // 
    //     let ary = [];
    //     this.tableData_All.forEach((data) => {
    //       let item = ary.find((e) => {
    //         return e.value === data[prop];
    //       });
    //       if (!item) {
    //         ary.push({
    //           text: data[prop],
    //           value: data[prop]
    //         });
    //       }
    //     });
    //     this.filtersChaneg = true;
    //     col.filters = ary;
    //     col.filterMethod = this.filterHandler;
    //     col.filteredValue =
    //       col.filteredValue || col.filters.map((ele) => ele.value);
    //   } else if (Array.isArray(filters) && filters.length > 0) {
    //     // filterstrue
    //     let colInAll = this.tableColumns_All.find((c) => {
    //       return c.prop === col.prop;
    //     });

    //     // filterstruefiltersdata
    //     if (colInAll.filters === true) {
    //       col.filters = this.tableData_All.map((data) => {
    //         return {
    //           text: data[prop],
    //           value: data[prop]
    //         };
    //       });
    //     } else if (Array.isArray(filters)) {
    //       // filters
    //       col.filters = filters;
    //     }
    //     this.filtersChaneg = true;
    //     col.filterMethod = this.filterHandler;
    //     col.filteredValue =
    //       col.filteredValue || col.filters.map((ele) => ele.value);
    //   }
    //   if (children && children.length) {
    //     col.children = children.map((c) => {
    //       return this.handleFilters(c);
    //     });
    //   }
    //   return col;
    // },

    /**  */
    clearSelection() {
      let table = this.$refs['el-table'];
      table.clearSelection();
    },

    /** selected  true  */
    toggleRowSelection(row, selected) {
      if (row) {
        let table = this.$refs['el-table'];
        table.toggleRowSelection(row, selected);
      }
    },

    /** selected  true  */
    toggleRowSelectionByIndex(index, selected) {
      this.$nextTick(() => {
        let row = this.tableData_[index];
        if (row) {
          let table = this.$refs['el-table'];
          table.toggleRowSelection(row, selected);
        }
      });
    },

    /** expanded  true  */
    toggleRowExpansion(row, selected) {
      if (row) {
        let table = this.$refs['el-table'];
        table.toggleRowExpansion(row, selected);
      }
    },

    /** expanded  true  */
    toggleRowExpansionByIndex(index, selected) {
      this.$nextTick(() => {
        let row = this.tableData_[index];
        if (row) {
          let table = this.$refs['el-table'];
          table.toggleRowExpansion(row, selected);
        }
      });
    },

    /** tabletable */
    doLayout() {
      this.$nextTick(() => {
        let table = this.$refs['el-table'];
        table.doLayout();
      });
    },

    /**  Table proporder */
    sort(prop, order) {
      let table = this.$refs['el-table'];
      table.sort(prop, order);
    },

    /**  */
    getTableData() {
      return cloneDeep(this.tableData_);
    },

    /**  */
    // getClassName(column, index) {
    //   let { className } = column;
    //   if (this.hasChildren && index === 0) {
    //     className = className
    //       ? 'hasChildren-column ' + className
    //       : 'hasChildren-column';
    //   }
    //   return className;
    // },

    /** hasFixed */
    setHasFixed(hasFixed) {
      this.hasFixed = hasFixed;
    },

    /** hasFixed */
    // updateRefreshFlag() {
    //   this.refreshFlag = false;
    //   this.$nextTick(() => {
    //     this.refreshFlag = true;
    //   });
    // },

    /** filter */
    filterHandler(value, row, column) {
      const property = column['property'];
      return row[property] === value;
    },

    /**  */
    btnClick(item) {
      let params = {
        type: 'header',
        clickItem: item,
        selections: this.selections
      };
      this.$emit('header-click', params);
    },

    /**  */
    selectionChange(selection) {
      if (this.multipleTable === 'radio') {
        if (selection.length > 1) {
          this.$refs['el-table'].toggleRowSelection(this.selections[0]);
        } else {
          this.selections = selection;
          this.$emit('selection-change', selection);
        }
      } else {
        this.selections = selection;
        this.$emit('selection-change', selection);
      }
    },

    /**  */
    sortChange(params) {
      this.$emit('sort-change', params);
    },

    /**  */
    recursionSetValue(array, prop, value, childrenKey, rowKey) {
      array.forEach((row) => {
        if (rowKey === row[this.table_.rowKey]) {
          row[prop] = value;
        } else if (row[childrenKey] && row[childrenKey].length > 0) {
          this.recursionSetValue(
            row[childrenKey],
            prop,
            value,
            childrenKey,
            rowKey
          );
        }
      });
    },

    /**
     * change
     */
    rowDataChange(params) {
      // TODO: 
      const { prop, index, value, row } = params || {};
      if (this.table_.rowKey) {
        let { children: childrenKey } = this.table_.treeProps || {};
        let rowKey = row[this.table_.rowKey];
        this.recursionSetValue(
          this.tableData_,
          prop,
          value,
          childrenKey,
          rowKey
        );
      } else {
        try {
          this.tableData_[index][prop] = value;
        } catch (error) {
          console.log(error);
        }
      }
      this.$emit('row-change', params);
    },

    // /**  */
    // changeRow(row, index) {
    //   let data = this.tableData_[index];
    //   Object.keys(row).forEach((prop) => {
    //     if (data[prop] !== row[prop]) {
    //       // if (Object.prototype.hasOwnProperty.calll(data, prop)) {
    //       if (Object.hasOwn(data, prop)) {
    //         try {
    //           this.tableData_[index][prop] = row[prop];
    //         } catch (error) {
    //           console.log(error);
    //         }
    //       } else {
    //         this.$set(this.tableData_[index], prop, row[prop]);
    //       }

    //       let params = {
    //         prop,
    //         index,
    //         value: row[prop]
    //       };
    //       this.$emit('row-change', params);
    //     }
    //   });
    // },

    /**  */
    rowItemClick(params) {
      this.$emit('row-item-click', params);
    },

    /**  */
    // columnFilter(params) {
    //   // el-popovercolumnFilterfilteredValue
    //   let { value, column } = params;
    //   this.tableData_ = this.tableData_All.filter((ele) => {
    //     let flag = false;
    //     value.forEach((e) => {
    //       if (column.filterMethod(e, ele, column)) {
    //         flag = true;
    //       }
    //     });
    //     return flag;
    //   });
    // },

    /**  */
    visibleChange() {
      // console.log('visibleChange');
      this.doLayout();
    },

    /**  */
    cellClick(row, column, cell, event) {
      this.$emit('cell-click', row, column, cell, event);
    },

    /**  */
    cellDblclick(row, column, cell, event) {
      this.$emit('cell-dblclick', row, column, cell, event);
    },

    /**  */
    rowClick(row, column, event) {
      this.$emit('row-click', row, column, event);
    },

    /**  */
    rowContextmenu(row, column, event) {
      this.$emit('row-contextmenu', row, column, event);
    },

    /**  */
    rowDblclick(row, column, event) {
      this.$emit('row-dblclick', row, column, event);
    },

    /**  expandedRows expanded */
    expandChange(row, params) {
      this.$emit('expand-change', row, params);
    }
  },
  watch: {
    table: {
      handler(newVal) {
        this.handleTable(newVal);
      },
      immediate: true,
      deep: true
    },
    tableColumns: {
      handler(newVal) {
        this.handleTableColumns(newVal);
      },
      immediate: true,
      deep: true
    },
    tableData: {
      handler(newVal) {
        this.handleTableData(newVal);
        // fixedfixedtoptable
        this.$nextTick(() => {
          if (this.hasFixed) {
            this.doLayout();
          }
        });
      },
      immediate: true,
      deep: true
    },
    /** watchcomputed visibleColumnsv-modal*/
    visibleColumns: {
      handler(newVal) {
        this.visibleColumns_ = newVal;
      },
      immediate: true,
      deep: true
    },
    visibleColumns_: {
      handler() {
        // fixedfixedtoptable
        this.$nextTick(() => {
          if (this.hasFixed) {
            this.doLayout();
          }
        });
      },
      immediate: true,
      deep: true
    }
  }
};
</script>
<style lang="scss" scoped>
.rd-table {
  // $mainTextColor: #333333; //

  // .el-table--border th,
  // .el-table--border td {
  //   border-right: 1px solid #aaa !important;
  // }

  // .el-table--group,
  // .el-table--border {
  //   border: 1px solid #aaa !important;
  // }

  // .el-table th.is-leaf,
  // .el-table td {
  //   border-bottom: 1px solid #aaa !important;
  // }

  // .el-table--border th {
  //   border-bottom: 1px solid #aaa !important;
  // }

  .el-table {
    //width: 100%;
    // font-size: 12px;

    // th,
    // td {
    //   color: $mainTextColor;
    //   padding: 5px 0;
    // }

    ::v-deep {
      // 
      thead {
        .multipleTable-radio .el-checkbox {
          display: none;
        }
        th {
          padding: 6px;
        }
        .cell {
          display: flex;
          align-items: center;
          justify-content: center;
          position: relative;
          padding: 0;

          // 
          // .el-table__column-filter-trigger {
          //   display: none;
          // }
        }
      }
    }
  }

  // .hasChildren {
  //   .el-table__indent {
  //     display: none;
  //   }

  //   .cell {
  //     padding: 0;

  //     .el-table__expand-icon {
  //       width: 12px;
  //       line-height: 12px;
  //       height: 12px;
  //       text-align: center;
  //     }
  //   }
  // }

  // .el-table__row--level-1 {
  //   .hasChildren-column {
  //     padding-left: 15px;
  //   }
  // }

  // .el-table__row--level-2 {
  //   .hasChildren-column {
  //     padding-left: 30px;
  //   }
  // }

  &-header {
    display: flex;
    flex-wrap: wrap;
    width: 100%;

    &-left {
      display: flex;
      flex: 1;
      padding-bottom: 10px;
    }

    &-right {
      display: flex;
      padding-bottom: 10px;

      .visibleCols {
        cursor: pointer;
        padding-top: 7px;
      }
    }

    &-item {
      margin-right: 5px;
      line-height: 23px;
      font-size: 12px;
    }
  }

  // &-body {
  //   background-color: #fff;
  // }

  .f-center {
    display: flex;
    justify-content: center;
  }
}

.visibleCols-list {
  max-width: 200px;
  max-height: 500px;
  overflow: hidden;
  overflow-y: auto;

  .visibleCols-item {
    display: flex;

    .ellipsis {
      margin-left: 10px;
      color: #409eff;
      display: block;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
}
</style>
