/**
 * formatDate
 * @param dateinit 13
 * @param format 'yyyy-MM-dd hh:mm:ss'
 * @returns {string} format
 */
const dateFormat = function(dateinit, format = 'yyyy/MM/dd hh:mm:ss') {
  let fmt = format;
  if (
    dateinit === null ||
    dateinit === 0 ||
    dateinit === '' ||
    dateinit === undefined
  ) {
    return '';
  }
  let date = new Date(dateinit);
  // let date = new Date(dateinit / 1000);dateinit10

  var o = {
    'M+': date.getMonth() + 1, // 
    'd+': date.getDate(), // 
    'h+': date.getHours(), // 
    'm+': date.getMinutes(), // 
    's+': date.getSeconds(), // 
    'q+': Math.floor((date.getMonth() + 3) / 3), // 
    S: date.getMilliseconds() // 
  };
  if (/(y+)/.test(fmt)) {
    fmt = fmt.replace(
      RegExp.$1,
      (date.getFullYear() + '').substring(4 - RegExp.$1.length)
    );
  }
  for (var k in o) {
    if (new RegExp('(' + k + ')').test(fmt)) {
      fmt = fmt.replace(
        RegExp.$1,
        RegExp.$1.length === 1
          ? o[k]
          : ('00' + o[k]).substring(('' + o[k]).length)
      );
    }
  }
  return fmt;
};

/**
 * 
 * @param {*} n
 * @returns
 */
const convertCurrency = function(n) {
  var fraction = ['', ''];
  var digit = ['', '', '', '', '', '', '', '', '', ''];
  var unit = [['', '', ''], ['', '', '', '']];
  var head = n < 0 ? '' : '';
  n = Math.abs(n);
  var s = '';
  for (let i = 0; i < fraction.length; i++) {
    s += (
      digit[Math.floor(n * 10 * Math.pow(10, i)) % 10] + fraction[i]
    ).replace(/./, '');
  }
  s = s || '';
  n = Math.floor(n);
  for (let i = 0; i < unit[0].length && n > 0; i++) {
    var p = '';
    for (var j = 0; j < unit[1].length && n > 0; j++) {
      p = digit[n % 10] + unit[1][j] + p;
      n = Math.floor(n / 10);
    }
    s = p.replace(/(.)*$/, '').replace(/^$/, '') + unit[0][i] + s;
  }
  return (
    head +
    s
      .replace(/(.)*/, '')
      .replace(/(.)+/g, '')
      .replace(/^$/, '')
  );
};

/**
 * 
 * @param {*} num 
 * @param {*} decimals 
 * @returns
 */
const numberThousandth = function(num = 0, decimals) {
  // 
  if (decimals) {
    return num.toFixed(decimals).replace(/(\d)(?=(\d{3})+\.)/g, '$1,'); // ','
  } else {
    return String(num).replace(/\d{1,3}(?=(\d{3})+$)/g, '$&,');
  }
};

/**
 * 
 * @param str 
 * @returns 
 */
const desensitization = function(str) {
  let _str = str || '';

  return `${_str}`.replace(/(\d{3})\d*(\d{4})/, '$1****$2');
};

export default {
  dateFormat,
  convertCurrency,
  numberThousandth,
  desensitization
};
