// 
const propertys = [
  'abort',
  'error',
  'load',
  'loadend',
  'loadstart',
  'progress',
  'timeout',
  'success',
  'type',
  'async',
  'url',
  'data',
  'params',
  'contentType'
];
/**
 *
 * @description -
 * @param {*} [options={}]
 */
function xhrHandle(options = {}) {
  const type = options.type || 'get'; // get
  const async = options.async || true; // true
  var url = options.url || location.origin; // 
  let header = options.header || {};

  var contentType =
    options.contentType ||
    header['Content-Type'] ||
    'application/json;charset=UTF-8'; // 
  header['Content-Type'] = contentType;
  var data; // datapost
  // 
  if (contentType.indexOf('json') > -1 && options.data) {
    data = JSON.stringify(options.data);
  } else {
    data = buildParams(options.data);
  }
  // url
  if (options.params) {
    url += '?' + buildParams(options.params);
  }
  // xhr
  var xhr = new XMLHttpRequest();
  // xhr
  for (var key of propertys) {
    xhr[key] = options[key];
  }
  // xhrsuccess
  xhr.onreadystatechange = function () {
    // readyState:4 
    // status:200 
    if (xhr.readyState === 4 && xhr.status === 200) {
      // success
      let res = xhr.response;
      try {
        res = JSON.parse(res);
      } catch (error) {
        console.log(error);
      }

      options.success && options.success(res);
    }
  };
  // 
  xhr.open(type, url, async);
  // 
  Object.keys(header).forEach((key) => {
    xhr.setRequestHeader(key, header[key]);
  });
  // 
  xhr.send(data);
}

/**
 *
 * @description params
 * @param {*} obj
 * @return {*}
 */
function buildParams(obj) {
  var vs = [];
  for (var key in obj) {
    vs.push(key + '=' + obj[key]);
  }
  return vs.join('&'); //   &  
}

export function post(url, data, success, contentType) {
  xhrHandle({ type: 'post', url, data, success, contentType });
}

export function get(url, params, success) {
  xhrHandle({ type: 'get', url, params, success });
}

export default xhrHandle;
