<template>
  <rd-dialog
    v-bind="dialogConfig"
    :visible.sync="dialogVisible"
    :close-on-click-modal="false"
    :append-to-body="true"
    custom-class="form-open-dialog"
    @confirm="confirm"
  >
    <div class="dialog-body" :style="{ height: boxHeight }">
      <rd-report
        v-if="dialogVisible"
        ref="rdReport"
        :table="table"
        :filters="filters"
        :headers="headers"
        :table-columns="tableColumns"
        :http-config="httpConfig"
        @selection-change="selectionChange"
        @row-dblclick="rowDblclick"
        @loadSuccess="loadSuccess"
      />
    </div>
  </rd-dialog>
</template>
<script>
import { cloneDeep } from 'lodash';
const typeMap = {
  prop: 1, // 
  props: 2 // valuelabel
};
export default {
  props: {
    /** dialog  */
    dialogConfig: {
      type: Object,
      default: () => {
        return {
          title: ''
        };
      }
    },
    /** rd-report  */
    reportConfig: {
      type: Object,
      default: () => {
        return {};
      }
    },
    multiple: Boolean,
    /**  */
    value: [String, Number, Array, Object],
    /**  */
    rowProps: {
      type: Object
    },
    type: Number
  },
  data() {
    return {
      typeMap,
      /** dialog */
      dialogVisible: false,

      /** report */
      table: {},
      filters: {},
      headers: {},
      tableColumns: [],
      httpConfig: {},
      selections: [],
      value_: null,

      // 
      boxHeight: null
    };
  },
  created() {
    this.initHeight();
  },
  mounted() {},
  methods: {
    /**  */
    initHeight() {
      let height = document.documentElement.clientHeight;
      this.boxHeight = height * 0.7 - 80 + 'px';
    },

    /**  */
    initReport(data) {
      let data_clone = cloneDeep(data);
      let { table, filters, headers, tableColumns, httpConfig } = data_clone;

      if (!tableColumns || !httpConfig) {
        return false;
      }

      let { multipleTable = 'radio' } = table || {};

      if (this.multiple) {
        multipleTable = true;
      }

      // filters
      let { config = {} } = filters;
      let { col = 12 } = config || {};

      config = {
        col,
        ...config
      };

      this.filters = {
        ...filters,
        config
      };
      this.headers = headers;
      this.table = {
        multipleTable,
        ...table
      };

      this.tableColumns = tableColumns;
      this.httpConfig = httpConfig;
    },

    /**  */
    open() {
      this.dialogVisible = true;
    },

    /**  */
    close() {
      this.dialogVisible = false;
      this.selections = [];
    },

    /**  */
    selectionChange(selection) {
      this.selections = selection;
      /**
       * 
       * 21
       * 2this.value_this.value_2,
       * pushthis.value_el-table
       * 
       *
       * 
       */
      // if (!this.multiple && selection.length > 0) {
      //   if (this.type === this.typeMap.props) {
      //     this.value_ = {
      //       value: selection[0][this.rowProps.value],
      //       label: selection[0][this.rowProps.label]
      //     };
      //   } else {
      //     this.value_ = selection[0][this.rowProps.value];
      //   }
      // }
    },

    /**
     * 
     */
    confirm() {
      if (!this.selections || !this.selections.length) {
        // Message.warning('');
        return;
      }
      this.doConfirm(this.selections);
    },

    /**  */
    rowDblclick(row) {
      let { selectable } = this.table;
      if (typeof selectable === 'function') {
        let flag = selectable(row);
        if (flag) {
          this.doConfirm([row]);
        }
      } else {
        this.doConfirm([row]);
      }
    },

    /**  */
    doConfirm(selections) {
      this.$emit('confirm', selections);
      this.close();
    },

    /** report  */
    loadSuccess(data) {
      this.$nextTick(() => {
        // if (!this.multiple) {
        // 
        if (Array.isArray(this.value_)) {
          data.forEach((row, index) => {
            let value = row[this.rowProps.value];

            let index_ = this.value_.findIndex((ele) => {
              if (this.type === this.typeMap.props) {
                return value === ele.value;
              } else {
                return value === ele;
              }
            });

            if (index_ > -1) {
              this.$refs.rdReport.toggleRowSelectionByIndex(index, true);
            }
          });
        } else {
          // if (this.type === this.typeMap.props) {
          if (
            (this.value_ || {}).value !== undefined &&
            (this.value_ || {}).value !== null &&
            (this.value_ || {}).value !== ''
          ) {
            data.forEach((row, index) => {
              let value = row[this.rowProps.value];
              if (value === (this.value_ || {}).value) {
                this.$refs.rdReport.toggleRowSelectionByIndex(index, true);
              }
            });
          }
          // } else {
          //   if (
          //     this.value_ !== undefined &&
          //     this.value_ !== null &&
          //     this.value_ !== ''
          //   ) {
          //     data.forEach((row, index) => {
          //       let value = row[this.rowProps.value];
          //       if (value === this.value_) {
          //         this.$refs.rdReport.toggleRowSelection(index, true);
          //       }
          //     });
          //   }
          // }
        }
        // }
      });
    }
  },
  watch: {
    reportConfig: {
      handler(newVal) {
        if (newVal) {
          this.initReport(newVal);
        }
      },
      deep: true,
      immediate: true
    },
    value: {
      handler(newVal) {
        this.value_ = cloneDeep(newVal);
      },
      deep: true,
      immediate: true
    }
  }
};
</script>
<style lang="scss" scoped>
.form-open-dialog {
  width: 100%;
}

.dialog-body {
  width: 100%;
}

::v-deep {
  .el-dialog__body {
    padding: 0 10px !important;

    .filters .filters-box {
      max-height: 200px;
    }
  }
}
</style>
